.pop-up {
    height: 5%;
    width: 5%; 
    margin: 0px;
    padding-top: 50px;
    background-color: aqua;
    position: absolute;

}

.popup-button {
    /* Add any styling to make it look like a <p> tag */
    display: inline;
    font-size: 1rem;
    font-weight: normal;
    color: black;
    text-decoration: none;
    background: none;
    border: none;
    cursor: pointer;
    /* Add any other styles you want to make it look like a <p> tag */
  }

.popup-card {
    border: 1px solid #ccc;
  border-radius: 4px;
  padding: 5px;
  margin-bottom: 20px;
  background-color:#333;
  padding-bottom: 2%;
  padding-right: 2%;
  padding-left: 2%;

  width: -webkit-fit-content;

  width: -moz-fit-content;

  width: fit-content;
  margin: 10px;
  justify-content:center;
  justify-self: center;
  align-self: center;
  align-content: center;
  text-align: center;
  display: flex;
  
}


.popup-description h1 {

    position:relative;
    font-size: 15px;
    margin-bottom: 0px;
    display: inline;
    

  }

.popup-description p {
padding-top: 5%;
position:relative;
